/* lower section */

.signup-email-password {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 6rem;
  /* max-width: 450px;
  margin-left: 25vw;
  margin-right: 25vw; */
