.signup-form-intro {
  font-size: 2.5rem;
  padding-bottom: .5rem;
}

.signup-form-subintro {
  font-size: 1.3rem;
  padding-bottom: 3rem;
}

/* signup form */

.signup-form {
  font-size: 1.15rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 3rem;
  border-radius: 1rem;
  -webkit-box-shadow: 0px 0px 10px 0px #888888;
          box-shadow: 0px 0px 10px 0px #888888;
