}

.signup-form > label {
  margin-bottom: .3rem;
}

.signup-form > input {
  margin-bottom: .8rem;
  padding: .2rem;
  width: 100%;
}

.signup-form > button {
  background-color:rgba(15, 189, 15, 1);
  color: white;
  border-radius: .3rem;
  height: 2.3rem;
  font-size: 1.3rem;
  margin-left: .2rem;
  margin-top: 1rem;
}


